<template>
  <div class="users">
    <b-row class="justify-content-center p-2" style="margin:0; padding:0; ">
      <Users/>
    </b-row>
    <b-row class="justify-content-center">
        <div class="d-flex flex-row">
          <b-button-group>
            <div class="p-2">
              <b-button @click="toggleUserForm" variant="outline-success">add new <b-icon icon="plus-circle"></b-icon></b-button>
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="outline-primary"
                >update <b-icon icon="check"></b-icon></b-button
              >
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="outline-danger"
                >delete <b-icon icon="trash"></b-icon></b-button
              >
            </div>
          </b-button-group>
          </div>
    </b-row>
    <div class="justify-content-center">
    <b-container>
    <b-form @submit.prevent="handleSubmit" v-if="showUserForm" style=" padding:20px;">
      <b-form-group id="input1" label="User id" label-for="id">
        <b-form-input
          id="id"
          type="text"
          v-model="formData.code"
          required
          placeholder="enter User id"
        />
      </b-form-group>
      <b-form-group id="input2" label="User Name" label-for="name">
        <b-form-input
          id="name"
          type="text"
          v-model="formData.title"
          required
          placeholder="enter user name"
        />
        </b-form-group>
        <b-form-group id="input3" label="Gender" label-for="gender">
        <b-form-input
          id="gender"
          type="text"
          v-model="formData.credit"
          required
          placeholder="enter gender"
        />
      </b-form-group>
      <b-form-group id="input4" label="User Role" label-for="role">
        <b-form-input
          id="role"
          type="text"
          v-model="formData.description"
          required
          placeholder="enter user role"
        />
      </b-form-group>
      <b-button-group>
        <b-button type="submit" variant="outline-primary">Submit</b-button>
        <b-button type="reset" variant="outline-primary">Reset</b-button>
      </b-button-group>
    </b-form>
    </b-container>
    </div>
  </div>
</template>
<script>
import Users from '@/components/Users.vue'
export default {
  components: {
    Users
  },
  data () {
    return {
      showUserForm: false,
      formData: {
        id: '',
        name: '',
        gender: '',
        role: ''
      }
    }
  },
  methods: {
    toggleUserForm () {
      this.showUserForm = !this.showUserForm
    }
  }
}
</script>
