<template>
  <div class="courses">
    <b-row class="justify-content-md-center p-4" style="margin:0; padding:0; ">
      <Courses/>
    </b-row>
     <b-row class="justify-content-center">
        <div class="d-flex flex-row">
          <b-button-group>
            <div class="p-2">
              <b-button @click="toggleCourseForm" variant="outline-success">add new <b-icon icon="plus-circle"></b-icon></b-button>
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="outline-primary"
                >update <b-icon icon="check"></b-icon></b-button
              >
            </div>
            <div class="p-2">
              <b-button class="main-navigation-button" variant="outline-danger"
                >delete <b-icon icon="trash"></b-icon></b-button
              >
            </div>
          </b-button-group>
          </div>
    </b-row>
    <b-container>
    <b-form @submit.prevent="handleSubmit" v-if="showCourseForm">
      <b-form-group id="input1" label="Course Code" label-for="code">
        <b-form-input
          id="code"
          type="text"
          v-model="formData.code"
          required
          placeholder="enter course code"
        />
      </b-form-group>
      <b-form-group id="input2" label="Course Title" label-for="title">
        <b-form-input
          id="title"
          type="text"
          v-model="formData.title"
          required
          placeholder="enter course title"
        />
        </b-form-group>
        <b-form-group id="input3" label="Credit Unit" label-for="credit">
        <b-form-input
          id="credit"
          type="number"
          v-model="formData.credit"
          required
          placeholder="enter credit unit"
        />
      </b-form-group>
      <b-form-group id="input4" label="Course Description" label-for="description">
        <b-form-input
          id="description"
          type="text"
          v-model="formData.description"
          required
          placeholder="enter course description"
        />
      </b-form-group>
      <b-button-group>
        <b-button type="submit" variant="outline-primary">Submit</b-button>
        <b-button type="reset" variant="outline-primary">Reset</b-button>
      </b-button-group>
    </b-form>
    </b-container>
  </div>
</template>
<script>
import Courses from '@/components/Courses.vue'
import { mapActions } from 'vuex'
export default {
  components: {
    Courses
  },
  data () {
    return {
      showCourseForm: false,
      formData: {
        code: '',
        title: '',
        credit: 0,
        description: ''
      }
    }
  },
  methods: {
    ...mapActions([
      'addCourse'
    ]),
    toggleCourseForm () {
      this.showCourseForm = !this.showCourseForm
    },
    handleSubmit () {
      const { code, title, credit, description } = this.formData
      const c = {
        code,
        title,
        credit,
        description
      }
      this.addCourse(c)
    }
  }
}
</script>
